import {postUserImformation} from '../models/indexModel.js'
import {loginApi} from '../models/indexModel.js'

// 
export default  (prams)=>{
	// code
	uni.login({
		provider: 'weixin',
		success: res => {
			// cookie
			loginApi({
				code: res.code
			}).then(res => {
				var status = res.data.status
				if(status) {
					uni.getUserProfile({
						desc: '', // 
						success: (res) => {
							console.log(res)
							//
								postUserImformation({
									"avatarImage": "",
									"grade": "",
									"name": "",
									"nickName": res.userInfo.nickName,
									"phone": "",
									"qq": "",
									"school": "",
									"volAccount": ""
								}).then(res => {
									console.log(res)
								})
								
						}
					})
				}
				// cookie
				uni.setStorage({
					key: 'cookie',
					data: {
						cookie: res.data.c
					},
					success: function() {
						if (status) {
							console.log('');
						} else {
							console.log('');
						}
						console.log(uni.getStorageSync('cookie'))
					},
					fail() {}
				});
			}).catch(err => {
				console.log('', err)
			})
		}
	});

}
