import {
	config
} from './config.js'
import login from './login.js'
export const apiResquest = (prams) => { //prams API 

	// 
	let headerData = {
		'content-type': 'application/json',
		'c': uni.getStorageSync('cookie').cookie
	}

	let dataObj = null
	//GETPOST
	if (prams.method === "GET") {
		headerData = {
			'content-type': 'application/json',
			'c': uni.getStorageSync('cookie').cookie
		}
	} else {
		dataObj = prams.query
	}
	return new Promise((resolve, reject) => {
		let url = config.base_url + prams.url; //api
		uni.showLoading({
			title: '',
			mask: true
		})
		if(prams.method === ''){
		
		}else{
			return uni.request({
				url: url,
				data: dataObj,
				method: prams.method,
				header: headerData,
				success: (res) => {
					uni.hideLoading()
					console.log("")
					if (res.data.code === -100) {
						console.log("")
						uni.showLoading({
							title:"",
							mask:true,
							success() {
								login()
							}
							
						})
						
					}
			
					resolve(res.data);
				},
				fail: (err) => {
					reject(err);
					console.log(err)
					console.log("")
					uni.hideLoading()
				},
				complete: () => {
					// console.log('')
					uni.hideLoading()
				}
			});
		}
		
	})
}




