import {
	apiResquest
} from '../utils/http.js'

//
export const getVolunteerRecords = (query) => {
	let str = query
	return apiResquest({
		url: `/signUp/over/${str}`,
		method: 'GET'

	})
}

//
export const postActicityFeeling = (query)=>{
	return apiResquest({
		url:'/impressions',
		method:'POST',
		query: query
	})
}
	
// 
export const getActicityFeeling = (query) => {
	let str = query
	return apiResquest({
		url: `/impressions/${str}`,
		method: 'GET'

	})
}
