import {
	apiResquest
} from '../utils/http.js'
//POST 

// 
export const loginApi = (query) => {
	return apiResquest({
		url: '/user/login',
		method: 'POST',
		query: query
	})
}




// 
export const getUserImformation = (query) => {
	return apiResquest({
		url: '/user',
		method: 'GET',
		query: query
	})
}
// 
export const postEmail = (query) => {
	return apiResquest({
		url: '/user/mailVerify',
		method: 'POST',
		query: query
	})
}
//
export const postEmailCode = (query) => {
	return apiResquest({
		url: '/user/codeCheck',
		method: 'POST',
		query: query
	})
}

// 
export const postUserImformation = (query) => {
	return apiResquest({
		url: '/user',
		method: 'PUT',
		query: query
	})
}
//
export const getVolunteer = (query) => {
	let str = query
	return apiResquest({
		url: `/activity/index/${str}`,
		method: 'GET',
	})
}
//
export const getActivityDetail = (query) => {
	let str = query
	return apiResquest({
		url: `/activityChild/${str}`,
		method: 'GET',
	})
}

//
export const cancelSignUp = (query) => {
	return apiResquest({
		url: '/signUp',
		method: 'DELETE',
		query: query
	})
}


